import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/views/Login.vue";
import Welcome from "@/views/Welcome.vue";
import Swipers from "@/views/Swipers.vue";
import Message from "@/views/Message.vue";
import Write from "@/views/Write.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login,
    meta: {
      needLogin: false
    }
  },
  {
    path: "/swipers",
    name: "Swipers",
    component: Swipers,
    meta: {
      needLogin: true
    }
  },
  {
    path: "/welcome",
    name: "Welcome",
    component: Welcome,
    meta: {
      needLogin: true
    }
  },

  {
    path: "/message",
    name: "Message",
    component: Message,
    meta: {
      needLogin: true
    }
  },
  {
    path: "/write",
    name: "Write",
    component: Write,
    meta: {
      needLogin: true
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
