import Vue from "vue";
import Vuex from "vuex";

import persistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    uid: "",
    token: ""
  },
  mutations: {
    setUid(state, uid) {
      state.uid = uid;
    },
    setToken(state, token) {
      state.token = token;
    }
  },
  plugins: [persistedState()]
});
