const imgPreloader = url => {
  return new Promise((resolve, reject) => {
    let image = new Image();
    image.src = url;
    image.onload = () => {
      document.querySelector(".pregrass").innerHTML = (
        document.querySelector(".pregrass").innerHTML * 1 +
        0.6
      ).toFixed(1);
      resolve();
    };
    image.onerror = () => {
      document.querySelector(".pregrass").innerHTML = (
        document.querySelector(".pregrass").innerHTML * 1 +
        0.6
      ).toFixed(1);
      reject();
    };
  });
};
