export const imgsPreloader = val => {
  let promiseArr = [];
  val.images.forEach(element => {
    promiseArr.push(imgPreloader(element));
  });
  // val.audios.forEach(element => {
  //   promiseArr.push(audioPreloader(element));
  // });
  return Promise.all(promiseArr);
};
