<template>
  <div class="message-page" @click="helpPlay">
    <video
      ref="writeAudio"
      src="@/assets/audio/message.mp3"
      class="message-audio"
      autoplay
      webkit-playsinline
      playsinline
      x5-playsinline
    ></video>
    <van-image
      class="flowers-video"
      height="5rem"
      fit="contain"
      :src="require('@/assets/image/leave-message.jpg')"
      @click="preview(0)"
    >
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
      <template v-slot:error>加载失败</template>
    </van-image>
    <van-sticky>
      <header class="title-box">
        <div class="title">
          <div
            class="text"
            :class="{active:active == index}"
            v-for="(item, index) in tabList"
            :key="index"
            @click="change(index)"
          >{{ item }}</div>
        </div>
        <div class="write" @click="write">
          <img src="@/assets/image/write.png" class="write-icon" alt />写留言
        </div>
      </header>
    </van-sticky>
    <template v-if="!active && topList.length">
      <div class="message no-bottom">
        <MessageItem :item="topList[0]" :index="0" :top="true" @flower="flower"></MessageItem>
      </div>
      <div class="top-list"></div>
    </template>
    <div class="soft-box">
      <div class="soft" @click="soft('createtime')">
        <img class="soft-img" src="@/assets/image/soft.png" alt /> 按时间
      </div>
      <div class="soft" @click="soft('praise')">
        <img class="soft-img" src="@/assets/image/soft.png" alt /> 按热度
      </div>
    </div>
    <template v-if="active === 0">
      <ul class="message-box" v-if="messageList.length">
        <li class="message" v-for="(item,index) in messageList" :key="index">
          <MessageItem :item="item" :index="index" @flower="flower"></MessageItem>
        </li>
      </ul>
      <div class="no-list" v-else>暂无留言</div>
      <van-pagination
        v-if="messageList.length"
        v-model="currentPage1"
        :page-count="total1"
        mode="simple"
        @change="getMessageList"
      />
    </template>
    <template v-else>
      <ul class="message-box" v-if="myList.length">
        <li class="message" v-for="(item,index) in myList" :key="index">
          <MessageItem :item="item" :index="index" @flower="flower"></MessageItem>
        </li>
      </ul>
      <div class="no-list" v-else>暂无留言</div>
      <van-pagination
        v-if="myList.length"
        v-model="currentPage2"
        :page-count="total2"
        mode="simple"
        @change="getMyList"
      />
    </template>
  </div>
</template>

<script>
import { Sticky, Pagination, Image, ImagePreview, Loading } from "vant";
import http from "@/utils/api";
import MessageItem from "@/components/message-item";

export default {
  components: {
    [Sticky.name]: Sticky,
    [Pagination.name]: Pagination,
    [Image.name]: Image,
    [ImagePreview.name]: ImagePreview,
    [Loading.name]: Loading,
    MessageItem
  },
  data() {
    return {
      autoPlay: false,
      topList: [],
      messageList: [],
      myList: [],
      tabList: ["留言板", "我的"],
      active: 0,
      currentPage1: 1,
      total1: 1,
      currentPage2: 1,
      total2: 1,
      orderby: "praise", // 默认热度排序
      isReverse: 0 // 默认正序
    };
  },
  mounted() {
    this.getMessageList(this.currentPage1);
    this.getMyList(this.currentPage2);
    this.getTopList();
  },
  methods: {
    change(index) {
      const methodsList = ["getMessageList", "getMyList"];
      this.active = index;
      this[methodsList[this.active]](this[`currentPage${this.active + 1}`]);
    },
    getMessageList(start) {
      http
        .getCommentList({
          start,
          orderby: this.orderby,
          isReverse: this.isReverse
        })
        .then(res => {
          this.messageList = res.list;
          this.total1 = res.total_page;
          console.log(res);
        });
    },
    getMyList(start) {
      http
        .getMyLeaverMessageList({
          start,
          orderby: this.orderby,
          isReverse: this.isReverse
        })
        .then(res => {
          this.myList = res.list;
          this.total2 = res.total_page;
          console.log(res);
        });
    },
    flower(index) {
      const { id, isFlower } = this.messageList[index];
      http.praise({ id, isFlower }).then(() => {
        if (isFlower) {
          this.messageList[index].isFlower = 0;
          this.messageList[index].praise--;
        } else {
          this.messageList[index].isFlower = 1;
          this.messageList[index].praise++;
        }
      });
    },
    soft(orderby) {
      this.orderby = orderby;
      this.isReverse = this.isReverse ? 0 : 1;
      this.getMessageList(1);
      this.getMyList(1);
    },
    write() {
      this.$router.push({ name: "Write" });
    },
    helpPlay() {
      if (!this.autoPlay) {
        this.autoPlay = true;
        this.play();
      }
    },
    play() {
      this.$refs.writeAudio.play();
    },
    preview(i) {
      ImagePreview({
        images: [require("@/assets/image/leave-message.jpg")],
        startPosition: i
      });
    },
    getTopList() {
      http.getTopList().then(res => {
        this.topList = res;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.message-page {
  height: fill-available;
  background-color: #fff;
  .flowers-video {
    width: 100%;
    background-color: black;
  }
  .title-box {
    width: 100%;
    padding: 0 0.3rem;
    height: 1rem;
    border-bottom: 0.01rem solid #ededed;
    box-sizing: border-box;
    @include flex(space-between, center, row, nowrap);
    background-color: #fff;
    .title {
      display: flex;
      .text {
        margin-right: 0.3rem;
        line-height: 0.6rem;
        box-sizing: border-box;
        @include font-style(0.34rem, #666, 400);
      }
      .active {
        border-bottom: 0.04rem solid #eb7350;
        @include font-style(0.34rem, #333, 500);
      }
    }
    .write {
      width: 1.8rem;
      height: 0.6rem;
      line-height: 0.6rem;
      background-color: #eb7350;
      border-radius: 0.4rem;
      @include font-style(0.28rem, #fff, 400);
      @include flex(flex-start, center, row, nowrap);
      .write-icon {
        width: 0.4rem;
        height: 0.4rem;
        margin-left: 0.2rem;
        margin-right: 0.15rem;
      }
    }
  }
  .soft-box {
    width: 100%;
    height: 0.7rem;
    box-sizing: border-box;
    padding-right: 0.3rem;
    @include flex(flex-end, center, row, nowrap);
    .soft {
      margin-left: 0.3rem;
      @include flex(flex-start, center, row, nowrap);
      @include font-style(0.24rem, #1989fa, 400);
      .soft-img {
        width: 0.25rem;
        height: 0.25rem;
        margin-right: 0.1rem;
      }
    }
  }

  .message-box {
    width: 100%;
    margin-bottom: 0.8rem;
    box-sizing: border-box;
  }
  .message {
    width: calc(100% - 0.6rem);
    margin: 0 0.3rem;
    padding: 0.3rem 0;
    border-bottom: 0.01rem solid #ededed;
    box-sizing: border-box;
    @include flex(flex-start, flex-start, column, nowrap);
    &:last-child {
      border: none;
    }
  }
  .no-bottom {
    border: none !important;
  }
  .top-list {
    width: 100%;
    height: 0.05rem;
    background-color: #ededed;
  }

  .no-list {
    height: fill-available;
    box-sizing: border-box;
    padding-top: 40%;
    background-color: #fff;
    text-align: center;
    @include font-style(0.28rem, #666, 400);
  }
}
.message-audio::-webkit-media-controls {
  display: none !important;
}
.message-audio {
  display: none !important;
}
</style>