import request from "./axios.config";
//如果请求多个服务器的api，可以这样定义
export default {
  /**
   * @description: 登录
   * @param { username, password }
   * @return: Promise
   */
  login(params) {
    return request({
      url: "/api/index/login",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 增加留言
   * @param {content}
   * @return:
   */
  addComment(params) {
    return request({
      url: "/api/index/addComment",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 获取所有留言
   * @param {start}
   * @return:
   */
  getCommentList(params) {
    return request({
      url: "/api/index/getCommentList",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 获取我的留言
   * @param {start}
   * @return:
   */
  getMyLeaverMessageList(params) {
    return request({
      url: "/api/index/getMyLeaverMessageList",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 点赞
   * @param {id，isFlower}
   * @return:
   */
  praise(params) {
    return request({
      url: "/api/index/praise",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 获取置顶
   * @param {}
   * @return:
   */
  getTopList() {
    return request({
      url: "/api/index/getTopList",
      method: "post"
    });
  }
};
