<template>
  <div class="item">
    <div class="title">
      <div class="desc-wrapper name">
        <img class="dot" src="@/assets/image/guide.jpg" alt />十七周年继续宠爱张国荣
      </div>
    </div>
    <div class="title">
      <div class="desc-wrapper">
        <img class="dot square" v-if="type === 'film'" src="@/assets/image/film.png" alt />
        <img class="dot square" v-if="type === 'music'" src="@/assets/image/music.png" alt />
        <div class="desc-name">{{ name }}</div>
        <img
          class="audioa"
          v-if="!last"
          :class="{animation: isPlay}"
          src="@/assets/image/audio-a.png"
          alt
          @click.stop="audio"
        />
        <div class="enter-box" v-else @click.stop="next">
          去留言小屋
          <img class="enter" src="@/assets/image/enter-666.png" alt />
        </div>
      </div>
      <div class="desc">{{ Introduction }}</div>
    </div>
    <div class="title">
      <div class="desc-wrapper">
        <img class="dot square" src="@/assets/image/photo.png" alt />精选图片
      </div>
      <div class="img-box">
        <van-swipe class="swiper" :show-indicators="false" :autoplay="3000">
          <van-swipe-item v-for="(i, idx) in imageList" :key="idx">
            <van-image
              width="6.3rem"
              height="4rem"
              fit="contain"
              :src="i"
              @click.stop="preview(idx)"
            >
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
              <template v-slot:error>加载失败</template>
            </van-image>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Image, ImagePreview, Loading } from "vant";
import "swiper/css/swiper.css";

export default {
  props: ["last", "name", "Introduction", "imageList", "type"],
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image,
    [ImagePreview.name]: ImagePreview,
    [Loading.name]: Loading
  },
  data() {
    return {
      isPlay: true
    };
  },
  methods: {
    preview(i) {
      ImagePreview({
        images: this.imageList,
        startPosition: i
      });
    },
    next() {
      this.$router.push({ name: "Welcome" });
    },
    audio() {
      this.isPlay ? this.$emit("pause") : this.$emit("play");
      this.isPlay = !this.isPlay;
    }
  },
  
};
</script>

<style lang="scss" scoped>
.item {
  width: 100%;
  min-height: fill-available;
  background-color: #f4f4f4;

  .title {
    margin: 0.3rem;
    border-radius: 0.1rem;
    background-color: #fff;

    .desc-wrapper {
      line-height: 1rem;
      box-sizing: border-box;
      border-bottom: 0.01rem solid #ededed;
      letter-spacing: 0.01rem;
      @include font-style(0.36rem, #333, 600);
      @include flex(flex-start, center, row, nowrap);
      .desc-name {
        flex: 1;
        @include font-style(0.36rem, #333, 600);
      }
      .audioa {
        margin-right: 0.3rem;
        width: 0.5rem;
        height: 0.5rem;
      }
      .enter-box {
        width: 2.3rem;
        padding: 0 0.2rem;
        box-sizing: border-box;
        animation: transX 1s linear 0 infinite normal none;
        @include font-style(0.28rem, #eb7350, 400);
        @include flex(center, center, row, nowrap);
        .enter {
          width: 0.3rem;
          height: 0.3rem;
        }
      }
      .animation {
        animation: rotateAudio 5s linear 0 infinite normal none;
      }
    }
    .name {
      border: none;
    }
    .desc {
      width: 100%;
      padding: 0.3rem;
      box-sizing: border-box;
      @include font-style(0.28rem, #333, 400);
    }

    .img-box {
      width: 100%;
      padding: 0.3rem;
      box-sizing: border-box;
      @include flex(center, center, row, nowrap);
      .swiper {
        width: 100%;
        height: 100%;
      }
    }
  }
  .dot {
    margin-left: 0.3rem;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 100%;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
  }
  .square {
    border-radius: 0;
  }
}
@keyframes rotateAudio {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>