import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import { imgsPreloader } from "./utils/imgPreloader.js";
import imgPreloaderList from "./utils/imgPreloaderList.js";

import "@/utils/rem.js";
import "@/utils/reset-mobile.css";

Vue.config.productionTip = false;

(async () => {
  await imgsPreloader(imgPreloaderList);
  //关闭加载弹框
  document.querySelector(".loading").style.display = "none";
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
})();

//路由守卫
router.beforeEach((to, from, next) => {
  if (store.state.uid && store.state.token) {
    // console.log(to.path) //每次跳转的路径
    if (to.path === "/") {
      //登录状态下 访问login.vue页面 会跳到 swipers.vue
      next({ path: "/swipers" });
    } else {
      next();
    }
  } else {
    // 如果没有session ,访问任何页面。都会进入到 登录页
    if (to.path === "/") {
      // 如果是登录页面的话，直接next() -->解决注销后的循环执行bug
      next();
    } else {
      // 否则 跳转到登录页面
      next({ path: "/" });
    }
  }
});
