import axios from "axios";
import md5 from "./md5";
import store from "@/store";
import qs from "qs";
import { Toast } from "vant";

const instance = axios.create({
  baseURL: "http://leslie.markcolin.cn", //基础URL,如果请求多个服务器的api，可以不用直接定义
  timeout: 5000, //请求延时时间
  headers: {
    "X-Requested-With": "XMLHttpRequest",
    "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
  }, //自定义请求头内容

  responseType: "json" //请求数据类型包括  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  //  params: {},  //无论请求为何种类型，在params中的属性都会以key=value的格式在url中拼接
  //  proxy: {
  //    //代理
  //     host: '127.0.0.1',
  //     port: 9000,
  //     auth: {
  //       username: 'mikeymike',
  //       password: 'rapunz3l'
  //     }
  // },
});

// 自定义添加请求拦截器
instance.interceptors.request.use(
  function(config) {
    // 在发送请求之前做些什么
    const { uid, token } = store.state;
    const timestamp = Math.round(new Date().getTime() / 1000).toString();
    const sign = `uid=${uid}&timestamp=${timestamp}&token=${token}`;

    let customHeader = { timestamp, uid, sign: md5(sign) };
    Object.assign(config.headers, customHeader);

    if (config.method == "POST" || config.method == "post") {
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  function(error) {
    // 对请求错误做些什么
    // Message.error('参数错误')
    return Promise.reject(error);
  }
);

// 自定义添加响应拦截器
instance.interceptors.response.use(
  function(response) {
    // 对响应数据做点什么
    if (response.status === 200 && response.data.code === 1) {
      return response.data.data;
    } else {
      Toast(response.data.msg);
      return Promise.reject(response);
    }
  },
  function(error) {
    // 对响应错误做点什么
    // Toast(error.data.msg);
    console.log(error);
    return Promise.reject(error);
  }
);

//将配置暴露出去
export default instance;
