<style lang="scss" scoped>
.swiper {
  width: 100%;
  min-height: fill-available;
  background-color: #f4f4f4;
}
.nav-box {
  height: 1rem;
  // width: 100%;
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  padding-left: 0.3rem;
  box-sizing: border-box;
  @include flex(flex-start, center, row, nowrap);
  .nav-item {
    position: relative;
    @include flex(flex-start, center, row, nowrap);
    .mask {
      z-index: 9;
      position: absolute;
      top: 0;
      left: 0;
      width: 0.7rem;
      height: 0.7rem;
      border-radius: 100%;
      background-color: rgba(0, 0, 0, 0.5);
    }
    .line {
      width: 0.3rem;
      height: 0.02rem;
      background-color: #f4f4f4;
    }
  }
  .white {
    flex: 0 0 0.3rem;
    height: 100%;
  }
}
.message-audio::-webkit-media-controls {
