<template>
  <div class="login-page">
    <!-- <img class="header" src="@/assets/image/guide.jpg" alt /> -->
    <van-image
      class="header"
      width="3rem"
      height="3rem"
      round
      fit="cover"
      :src="require('@/assets/image/guide.jpg')"
    >
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
      <template v-slot:error>加载失败</template>
    </van-image>
    <div class="wrapper">
      <img class="input-icon" src="@/assets/image/user.png" alt />
      <input type="text" name="username" id="username" v-model.trim="username" placeholder="用户名" />
    </div>
    <div class="wrapper">
      <img class="input-icon" src="@/assets/image/password.png" alt />
      <input type="password" name="password" id="password" v-model.trim="password" placeholder="密码" />
    </div>
    <button
      class="login"
      :disabled="!username || !password"
      :class="{'can-login': username && password}"
      @click="login"
    >登录</button>
    <div class="tip">
      <span class="tip1"></span>
      <span class="tip2"></span>
      建议使用微信浏览器打开
      <span class="tip2"></span>
      <span class="tip1"></span>
    </div>
  </div>
</template>

<script>
import { Image, Loading } from "vant";
// import imgPreloaderList from "@/utils/imgPreloaderList.js";
import http from "@/utils/api";

export default {
  components: {
    [Image.name]: Image,
    [Loading.name]: Loading
  },
  data() {
    return {
      username: "",
      password: ""
    };
  },
  mounted() {
    // this.loadAudio(imgPreloaderList);
  },
  methods: {
    login() {
      // 登陆
      http
        .login({ username: this.username, password: this.password })
        .then((res) => {
          this.$store.commit('setUid',res.uid)
          this.$store.commit('setToken',res.token)
          this.$router.push({ name: "Swipers" });
        });
    },
    // loadAudio(val) {
    //   let promiseArr = [];
    //   val.audios.forEach(element => {
    //     promiseArr.push(this.audioPreloader(element));
    //   });
    //   return Promise.all(promiseArr);
    // },
    // audioPreloader(url) {
    //   return new Promise((resolve, reject) => {
    //     let audio = new Audio();
    //     audio.src = url;
    //     audio.onloadedmetadata = () => {
    //       resolve();
    //     };
    //     audio.onerror = () => {
    //       reject();
    //     };
    //   });
    // }
  }
};
</script>

<style lang="scss" scoped>
.login-page {
  height: fill-available;
  @include flex(flex-start, center, column, nowrap);
  .header {
    margin-top: 2.2rem;
    margin-bottom: 1.5rem;
    box-sizing: border-box;
    border: 0.01rem solid #ededed;
  }
  .wrapper {
    width: 4.8rem;
    height: 0.88rem;
    margin-bottom: 0.4rem;
    padding-bottom: 0.2rem;
    box-sizing: border-box;
    border-bottom: 0.01rem solid #ededed;
    @include flex(flex-start, center, row, nowrap);
    .input-icon {
      width: 0.5rem;
      height: 0.5rem;
      margin-right: 0.2rem;
    }
    input {
      flex: 1;
      height: 100%;
      line-height: 0.68rem;
      border: none;
      border-radius: 0.34rem;
      background: transparent;
      outline: none;
      @include font-style(0.33rem, #333, 400);
    }
    input[type="text"]:focus,
    input[type="password"]:focus {
      background: transparent;
      outline: none;
    }
  }

  .login {
    margin-top: 1rem;
    padding: 0;
    border: none;
    width: 4.8rem;
    height: 0.8rem;
    border-radius: 0.4rem;
    background-color: #f4f4f4;
    @include font-style(0.33rem, #333, 500);
    @include flex(center, center, row, nowrap);
  }
  .can-login {
    background-color: #07c160;
    color: #fff;
  }
  .tip {
    margin-top: 1rem;
    @include font-style(0.28rem, #999, 400);
    @include flex(center, center, row, nowrap);
    .tip1 {
      width: 0.5rem;
      height: 0.01rem;
      background-color: #999;
    }
    .tip2 {
      margin: 0 0.1rem;
      width: 0.02rem;
      height: 0.02rem;
      border-radius: 100%;
      background-color: #999;
    }
  }
}
</style>